import React from 'react';
import { graphql } from 'gatsby';
import LecturePage from 'containers/LecturePage';
import { LecturePageProps } from 'src/containers/LecturePage/types';

const LectureTemplate = (props: LecturePageProps) => {
  return <LecturePage {...props} />;
};

export default LectureTemplate;

export const pageQuery = graphql`
  query LectureBySlugQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(truncate: true)
      html
      tableOfContents(absolute: false, maxDepth: 3)
      fields {
        slug
        lecture
        category
      }
      frontmatter {
        title
      }
    }
  }
`;
