import React from 'react';
import { useLectureSummary } from 'hooks/useLectureSummary';
import { List, makeStyles, Theme } from '@material-ui/core';
import LectureTitle from './LectureTitle';
import LectureSection from './LectureSection';

const useStyles = makeStyles<Theme>(({ palette, spacing }) => ({
  root: {
    position: 'relative',
    borderRight: `1px dashed ${palette.divider}`,
    '&:before': {
      content: '""',
      position: 'absolute',
      bottom: 0,
      left: 0,
      width: '100%',
      height: spacing(10),
      background: `linear-gradient(0deg, ${palette.background.default} 0%, rgba(255,255,255,0) 100%)`,
      zIndex: 1,
    },
  },
  list: {
    height: '100vh',
    overflow: 'scroll',
    paddingBottom: spacing(10),
  },
}));

interface Props {
  lectureSlug: string;
}
const LectureSummary = ({ lectureSlug }: Props) => {
  const styles = useStyles();
  const { lecture, sections } = useLectureSummary(lectureSlug);
  return (
    <nav className={styles.root}>
      <List className={styles.list}>
        <LectureTitle to={lecture.fields.path}>{lecture.frontmatter.title}</LectureTitle>
        {sections.map(({ intro, contents }) => (
          <LectureSection intro={intro} contents={contents} key={intro.fields.slug} />
        ))}
      </List>
    </nav>
  );
};

export default LectureSummary;
