import { useStaticQuery, graphql } from 'gatsby';
import { Lecture, LectureSummary } from 'models/lecture';
import { generateSections, getLecture } from 'utils/lectures';

interface LectureQueryResult {
  allMarkdownRemark: {
    edges: Array<{
      node: Lecture;
    }>;
  };
}
export const useLectureSummary = (lectureSlug: string): LectureSummary => {
  const {
    allMarkdownRemark: { edges },
  } = useStaticQuery<LectureQueryResult>(graphql`
    query AllLecturesQuery {
      allMarkdownRemark(
        sort: { fields: fields___slug, order: ASC }
        filter: { fields: { mdType: { eq: "lectures" } } }
      ) {
        edges {
          node {
            ...LectureQueryFragment
          }
        }
      }
    }
  `);

  const lectures = edges.map(({ node }) => node).filter(node => node.fields.lecture === lectureSlug);
  const rootLecture = getLecture(lectures);

  if (rootLecture == null) {
    throw new Error('There is no root lecture');
  }

  const summary = {
    lecture: rootLecture,
    sections: generateSections(lectures),
  };

  return summary;
};
