import React, { useEffect } from 'react';
import { Box, makeStyles, Theme } from '@material-ui/core';
import Layout from 'components/Layout';
import SEO from 'components/SEO';
import { generateLogger } from 'utils/loggers';
import { LecturePageProps } from './types';
import LectureSummary from 'components/Lecture/LectureSummary';
import LectureContents from 'components/Lecture/LectureContents';

const logger = generateLogger('lecture_page');

const useStyles = makeStyles<Theme>(({ spacing }) => ({
  contentWrapper: {
    marginBottom: spacing(4),
  },
  tocWrapper: {
    marginLeft: spacing(1),
  },
  shareWrapper: {},
}));

const LecturePage = ({ data }: LecturePageProps) => {
  const styles = useStyles();
  const { markdownRemark: lecture } = data;
  const { frontmatter } = lecture;

  useEffect(() => {
    logger.view();
    logger.impression(`show_lecture`, { title: lecture.frontmatter.title });
  }, [lecture.frontmatter.title]);

  return (
    <Layout left={<LectureSummary lectureSlug={lecture.fields.lecture} />}>
      <SEO title={frontmatter.title} />
      <Box className={styles.contentWrapper}>
        <LectureContents lecture={lecture} />
      </Box>
    </Layout>
  );
};

export default LecturePage;
