import { Lecture, LectureSection } from 'src/models/lecture';

export function getLecture(lectures: Lecture[]) {
  return lectures.find(lecture => lecture.fields.category === 'lecture') as Lecture<'lecture'> | undefined;
}

export function generateSections(lectures: Lecture[]): LectureSection[] {
  const sections = lectures.filter(({ fields }) => fields.category === 'section') as Array<Lecture<'section'>>;
  const contents = lectures.filter(({ fields }) => fields.category === 'content') as Array<Lecture<'content'>>;

  return sections.map(section => ({
    intro: section,
    contents: contents.filter(({ fields: { slug } }) => {
      return section.fields.slug === slug.replace(/\.[^.]+$/, '');
    }),
  }));
}
