import { ListItem, makeStyles, Theme, Typography } from '@material-ui/core';
import React from 'react';
import { Link } from 'gatsby';

const useStyles = makeStyles<Theme>(({ spacing, typography }) => ({
  list: {
    marginBottom: spacing(2),
  },
  title: {
    fontWeight: typography.fontWeightBold,
  },
  link: {
    textDecoration: 'none',
    color: 'inherit',
  },
}));

interface Props {
  children: string;
  to: string;
}
const LectureTitle = ({ to, children }: Props) => {
  const styles = useStyles();
  console.log('style', styles.link);

  return (
    <Link to={to} className={styles.link}>
      <ListItem button className={styles.list}>
        <Typography variant="subtitle1" className={styles.title}>
          {children}
        </Typography>
      </ListItem>
    </Link>
  );
};

export default LectureTitle;
