import React, { memo } from 'react';
import Markdown from 'components/Utils/Markdown';
import LectureHeader from './Header';
import { Lecture } from 'src/models/lecture';

interface Props {
  lecture: Lecture;
}
const LectureContents = ({ lecture }: Props) => {
  const {
    frontmatter: { title },
    html,
  } = lecture;

  return (
    <>
      <LectureHeader title={title ?? ''} />
      <Markdown html={html ?? ''} />
    </>
  );
};

export default memo(LectureContents);
