import { ListItem, makeStyles, Theme, Typography } from '@material-ui/core';
import React from 'react';
import { Link } from 'gatsby';
import { Lecture } from 'src/models/lecture';

const useStyles = makeStyles<Theme>(({ typography, spacing }) => ({
  root: {
    marginBottom: spacing(2),
  },
  sectionTitleText: {
    fontWeight: typography.fontWeightBold,
  },
  contentListItem: {
    paddingLeft: spacing(4),
  },
  link: {
    textDecoration: 'none',
    color: 'inherit',
  },
}));

interface Props {
  intro: Lecture<'section'>;
  contents: Array<Lecture<'content'>>;
}
const LectureSection = ({ intro, contents }: Props) => {
  const styles = useStyles();

  return (
    <div className={styles.root}>
      <ListItem>
        <Typography variant="subtitle2" className={styles.sectionTitleText}>
          {intro.frontmatter.title}
        </Typography>
      </ListItem>
      {contents.map(content => (
        <Link to={content.fields.path} key={content.fields.slug} className={styles.link}>
          <ListItem button className={styles.contentListItem}>
            <Typography variant="caption">{content.frontmatter.title}</Typography>
          </ListItem>
        </Link>
      ))}
    </div>
  );
};

export default LectureSection;
